import { UserService } from 'src/app/services/user.service';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu-exercicios',
  templateUrl: './menu-exercicios.page.html',
  styleUrls: ['./menu-exercicios.page.scss'],
})
export class MenuExerciciosPage implements OnInit {
  programa;
  constructor(
    private router: Router,
    private userSrv: UserService
    ) { }

  ngOnInit() {
    this.loadInfo()
  }

  goToWeekExercicios(){
    this.router.navigate(['week-exercicios'])
  }
  goToEvolucaoExercicios(){
    this.router.navigate(['workout-tracker'])
  }
  backToBegin(){
    this.router.navigate(['tabs/tab1'])
  }

  loadInfo(){
    this.userSrv.getUserPlan().subscribe((response)=>{
      this.programa = response.programa
      console.log(this.programa)
    })
  }
}
